.wrapper h1{
font-family: Playfair;
font-weight: 669;
letter-spacing: 1rem;
color: white;
font-size: 40px;
text-transform: uppercase;
}

.buttonWrapper{
  display: flex;
  justify-content: space-between;
  width: 70%;
}

.emojis{
    display: flex;
    width: 70%;
    justify-content: space-between;
    align-items: center;
}

.emojis i{
    height: 150px;
    width: 150px;
    margin-bottom: 20px;
}

.jokes{
    background-color: white;
    width: 70%;
    overflow-y: scroll;
    overflow-x: hidden;

}

.getFavouritesJokes{
    padding:1rem 1rem;
    border: 4px solid gold;
    font-size: 20px;
    margin-top: 15px;
    margin-bottom: 15px;
    transition: all 0.5s ease-in-out;
    color:goldenrod;
    
}
.getFavouritesJokes:hover{
    font-size: 22px;
    font-weight: bold;
    border:6px solid gold;
    transform: translateX(2px); 
    
}



.status{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(234, 225, 241);
    height: 100vh;
}

.status h1{
    font-family: PlayFair;
    font-weight: 669;
    font-size: 40px;
    margin:0px;
    color:red;
}

.em-slightly_smiling_face:hover,.em-neutral_face:hover,.em-dizzy_face:hover{
   animation-name: upDown;
   animation-duration: 0.6s;
   animation-delay: 0s;
   animation-iteration-count: infinite;
    }

.em-smile:hover,.em-laughing:hover,.em-joy:hover,.em-rolling_on_the_floor_laughing:hover{
    animation-name:shake;    
    animation-duration:0.6s;
    animation-iteration-count:infinite;
    animation-timing-function:linear;
    transform-origin:50% 100%;
}

.em-unamused:hover,.em-slightly_frowning_face:hover,.em-angry:hover{
    animation-name:angry;    
    animation-duration:0.6s;
    animation-iteration-count:infinite;
    animation-timing-function:linear;
    transform-origin:50% 100%;
}

@keyframes upDown{
    0% {transform: translateY(-2px)}
    25% {transform: translateY(-1px)}
    50% {transform: translateY(0px)}
    75% {transform: translateY(1px)}
    100% {transform:translateY(2px)}
}

@keyframes shake {
    0%  {transform:translate(2px, 1px) rotate(0deg); }
    10% {transform:translate(-1px, -2px) rotate(-2deg); }
    20% {transform:translate(-3px, 0px) rotate(3deg); }
    30% {transform:translate(0px, 2px) rotate(0deg); }
    40% {transform:translate(1px, -1px) rotate(1deg); }
    50% {transform:translate(-1px, 2px) rotate(-1deg); }
    60% {transform:translate(-3px, 1px) rotate(0deg); }
    70% {transform:translate(2px, 1px) rotate(-2deg); }
    80% {transform:translate(-1px, -1px) rotate(4deg); }
    90% {transform:translate(2px, 2px) rotate(0deg); }
    100%{transform:translate(1px, -2px) rotate(-1deg); }
   }



   @keyframes angry {
    0% {
      -webkit-transform: none;
      transform: none;
    }
  
    15% {
      -webkit-transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);
      transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);
    }
  
    30% {
      -webkit-transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg);
      transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg);
    }
  
    45% {
      -webkit-transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);
      transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);
    }
  
    60% {
      -webkit-transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);
      transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);
    }
  
    75% {
      -webkit-transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
      transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
    }
  
    100% {
      -webkit-transform: none;
      transform: none;
    }
  }




/* Medium devices (tablets, less than 992px)*/
@media (max-width: 1045px) { 
  .getFavouritesJokes{
    font-size: 16px;
    width:48%;
    margin-top: 10px;
    margin-bottom: 10px;  
}
.getFavouritesJokes:hover{
    font-size: 18px;
}

.wrapper h1{
  text-align: center;
  }

}


  /* Extra small devices (portrait phones, less than 576px) */
  @media (max-width: 649px) { 
    .buttonWrapper{
      width: 85%; 
      justify-content: center;
      flex-direction: column;
     
    }
  
     .getFavouritesJokes{
      width:100%; 
  }

  .emojis{
    width: 85%;  
}

.jokes{

  width: 85%;
 

}
  }
  